* {
  font-size: 18px;
}

.container {
  padding: 30px;
  max-width: 600px;
  margin: 20px auto;
  color: #fff;
  border-radius: 20px;
  background-color: rgb(0, 174, 255);
  height: 500px;
}

.form-group {
  display: flex; 
}

.form-input {
  width: 100%;
  height: 40px;
  outline: none;
  margin-right: 30px;
  border: none;
  border-bottom: 2px solid #fff;
  color: #fff;
  background-color: rgb(0, 174, 255);
}

::-webkit-input-placeholder {
  font-size: 18px;
  color: #fff;
}

.btn {
  padding: 0 30px;
  border-radius: 5px;
  border: none;
  font-size: 18px;
  color:#fff;
  cursor: pointer;
  background-color: red;
}

.btn:active {
  background-color: rgb(250, 70, 70);
}

.searching-preview {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.weather-preview {
  padding: 30px 0;
  margin: 0 auto;
  margin-top: 30px;
  width: 300px;
  height: 350px;
  border-radius: 20px;
  background-color: rgb(0, 147, 167);
}

.city-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  padding-bottom: 10px;
}

.city-name {
  font-size: 30px;
  font-weight: 700;
  color: rgb(255, 255, 255);
}

.daye-info {
  padding: 5px 20px;
  background-color: rgb(0, 119, 134);
}

.temperature {
  padding: 0 20px;
  padding-top: 10px;
  font-size: 60px;
  color: black
}

.weather-icon {
  padding: 0 20px;
  width: 150px;
  height: 150px;
}

.weather-info {
  padding: 0 20px;
}

.wind-info {
  padding-top: 10px;
}

.wind-speed {
  padding: 0 20px;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
}